import common from '../../common/common.js'
import util from '../../common/util.js'
	
export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			shopid:0,				//店铺ID
			shop:[],				//店铺信息
						
			id:0,
			article:[],			//文章
			
			menu:{
				mindex:3,
				shopid:0,
				shop:[],
			},
			
			info:{
				bg_color:'0,170,250',
				bg_alpha:1,
				delta:2,
				icon_color:'white',
				shpid:0,
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,	
		}
	},
	onLoad(option) {
		var that = this
		var shopid = option.shopid
		var id = option.id
		that.shopid = shopid
		that.id = id
		// id = 22;
				
		// shopid = 10;
		if(shopid > 0){
			that.menu.shopid = shopid
			that.info.shopid = shop.shop_id
			
			uni.setStorageSync('shopid', shopid);
			//获取店铺资讯
			that.getShopArticle();	
			//获取店铺信息
			that.getShopInfo();
		}
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
	
	},
	
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		
		//获取店铺资讯
		that.getShopArticle();
		//获取店铺信息
		that.getShopInfo();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},	
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		var shopid = that.shopid
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/articleview?shopid='+shopid+'&id='+that.id+'&tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/articleview?shopid='+shopid+'&id='+that.id
		}
		return {
		  title: that.article.title  + '-' + that.shop.shop_title,
		  path: shareUrl,
		}
	},
	methods: {
		//获取店铺信息
		getShopInfo(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopInfo',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.shop = res.data.shop
					that.menu.shop = res.data.shop
					var shop = res.data.shop
					uni.setNavigationBarTitle({
					    title: shop.shop_title
					});
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//跳转
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url:url,
			});
		},
		//获取店铺资讯
		getShopArticle(){
			var that = this
			var id = that.id
			if(id == '' || id == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopArticle',
				method: 'GET',
				data: {
					id:id
				},
				success: res => {
					that.article = res.data.article
					console.log(res)











				},
				fail: () => {},
				complete: () => {}
			});
		},
		
	}
}
